import './detectingJS';
import './menuToggle';

// Переменные полей формы
const firstName = document.querySelector('#first-name');
const surname = document.querySelector('#surname');
const patronymic = document.querySelector('#patronymic');
const phone = document.querySelector('#phone');
const mail = document.querySelector('#mail');
const devilBridge = document.querySelector('#devil-bridge');
const mountainBell = document.querySelector('#mountain-bell');
const slidePark = document.querySelector('#slide-park');
const redRocks = document.querySelector('#red-rocks');
const emotions = document.querySelector('.emotions');

const formFields = {
  firstName,
  surname,
  patronymic,
  phone,
  mail,
  devilBridge,
  mountainBell,
  slidePark,
  redRocks,
  emotions,
};

/**
 *  Временное хранилище для данных формы, чтобы лишний раз не обращаться к
 *  localStorage
 */
let temporaryDataStorageOfForm = {};

// Сохранить данные формы во временном хранилище
const savingFormDataToTemporaryStorage = (fieldName, value) => {
  temporaryDataStorageOfForm[fieldName] = value;
};

// Сохранить данные формы в localStorage
const savingFormDataToLocalStorage = (temporaryDataStorage) => {
  localStorage.setItem('feedbackFormData', JSON.stringify(temporaryDataStorage));
};

/** Проверить соответствует ли текущее значение поля сохранненому во
 * временном хранилище
 */
const isCheckStoredValue = (
  fieldToBeChecked,
  temporaryDataStorage,
  newValue,
) => {
  if (
    fieldToBeChecked in temporaryDataStorage
    && temporaryDataStorage[fieldToBeChecked] === newValue
  ) {
    return true;
  }
  return false;
};

// Обработчик для сохранения текстовых данных формы при потери фокуса
const textInputFieldBlurHandler = (e) => {
  e.preventDefault();
  const that = e.target;

  if (!isCheckStoredValue(that.id, temporaryDataStorageOfForm, that.value)) {
    savingFormDataToTemporaryStorage(that.id, that.value);
    savingFormDataToLocalStorage(temporaryDataStorageOfForm);
  }
};

// Обработчик для сохранения значений чекбоксов
const checkboxInputFieldChangeHandler = (e) => {
  e.preventDefault();
  const that = e.target;

  if (!isCheckStoredValue(that.id, temporaryDataStorageOfForm, that.checked)) {
    savingFormDataToTemporaryStorage(that.id, that.checked);
    savingFormDataToLocalStorage(temporaryDataStorageOfForm);
  }
};

// Навешываем обработчики для сохранения данных полей ввода
firstName.addEventListener('blur', textInputFieldBlurHandler);
surname.addEventListener('blur', textInputFieldBlurHandler);
patronymic.addEventListener('blur', textInputFieldBlurHandler);
phone.addEventListener('blur', textInputFieldBlurHandler);
mail.addEventListener('blur', textInputFieldBlurHandler);
devilBridge.addEventListener('change', checkboxInputFieldChangeHandler);
mountainBell.addEventListener('change', checkboxInputFieldChangeHandler);
slidePark.addEventListener('change', checkboxInputFieldChangeHandler);
redRocks.addEventListener('change', checkboxInputFieldChangeHandler);
emotions.addEventListener('blur', textInputFieldBlurHandler);

// Преобразовать строку в camelCase (из вида devil-bridge в devilBridge)
const convertStringToCamelCase = (str) => {
  let camelCaseStr = '';

  for (let i = 0; i < str.length; i += 1) {
    if (str[i] === '-') {
      camelCaseStr += str[i += 1].toUpperCase();
      i += 1;
    }
    camelCaseStr += str[i];
  }
  return camelCaseStr;
};

// Задать сохраненное ранее значение полю ввода
const setValueOfInputField = (field, value) => {
  if (formFields[field].type === 'checkbox') {
    formFields[field].checked = value;
  } else {
    formFields[field].value = value;
  }
};

// Проверить наличие данных формы в хранилище
const isCheckFormDataInLocalStorage = () => localStorage.length && 'feedbackFormData' in localStorage;

// Подгружаем данные формы после загрузки HTML и построенного DOM-дерева
const documentDomContentLoadedHandler = () => {
  // Достаём раннее введенные данные из localStorage во временный объект
  if (isCheckFormDataInLocalStorage()) {
    temporaryDataStorageOfForm = JSON.parse(
      localStorage.getItem('feedbackFormData'),
    );

    // Массив с собственными ключами. Не наследуемыми
    const arr = Object.keys(temporaryDataStorageOfForm);

    // Перебираем раннее введенные данные
    for (let i = 0; i < arr.length; i += 1) {
      // Задаем ранее сохраненные данные полям ввода
      setValueOfInputField(
        convertStringToCamelCase(arr[i]),
        temporaryDataStorageOfForm[arr[i]],
      );
    }
  }
};
/** Навешиваем обработчик событий для подгрузки данных формы после загрузки
 *  HTML и построенного DOM-дерева
 */
document.addEventListener(
  'DOMContentLoaded',
  documentDomContentLoadedHandler,
);

const submitFormButton = document.querySelector('.button-form');
const preloader = document.querySelector('.preloader-wrp');

// Обработчик отправки формы
const submitFormButtonClickHandler = (e) => {
  e.preventDefault();

  const req = new XMLHttpRequest();
  req.open('post', '/feedback', true);
  req.setRequestHeader('Content-type', 'application/json; charset=utf-8');

  // Тайм-аут соединения ( например, пропала сеть )
  const reqTimeout = setTimeout(() => {
    req.abort();
  }, 10000);

  // Обработчик состояния ответа
  req.onreadystatechange = () => {
    if (req.readyState !== 4) return;

    // Очистить таймаут при завершении запроса
    clearTimeout(reqTimeout);

    // Запрос сервером обработан успешно
    if (req.status === 201) {
      // Ответ от сервера, что все ок

      // Чистим временное хранилище раннее введенных данных формы
      temporaryDataStorageOfForm = null;

      // Очищаем localStorage от данных формы
      localStorage.removeItem('feedbackFormData');

      preloader.classList.add('preloader-wrp--hide');

      // Вывести модалку, что всё ОК
    } else { // Если код статуса ответа от сервера не 201
      preloader.classList.add('preloader-wrp--hide');
      // handError(req.statusText);
      // Вызвать обработчик ошибки с текстом ответа
    }
  };

  // Отобразить прелоадер пока отсылаются данные формы
  preloader.classList.remove('preloader-wrp--hide');

  req.send(JSON.stringify(temporaryDataStorageOfForm));
};

// Навешиваем обработчик на отправку формы
submitFormButton.addEventListener('click', submitFormButtonClickHandler);
