const mainNav = document.querySelector('.main-nav');
const menuToggle = document.querySelector('.menu-toggle');
const logo = document.querySelector('.logo');

// Открыть/скрыть меню при клике по гамбургеру
const menuToggleClickHandler = (e) => {
  e.preventDefault();

  // Сменить три полосы гамбургера на крестик
  menuToggle.classList.toggle('menu-toggle--open');

  // Открыть главное меню
  mainNav.classList.toggle('main-nav--show');

  // Скрыть логотип, что бы не загараживал меню на мобильниках
  logo.classList.toggle('hidden');
};

// Обработчик для открытия/скрытия меню
menuToggle.addEventListener('click', menuToggleClickHandler);
